"use client";

import { useTimer } from "@/hooks/useTimer";
import { useTheme } from "@/contexts/ThemeContext";
import { Play, Pause, RotateCcw, Coffee, Brain, Zap } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import clsx from "clsx";

export default function FocusPage() {
  const {
    timeLeft,
    isActive,
    mode,
    toggleTimer,
    resetTimer,
    setMode,
    formatTime,
  } = useTimer();
  const { theme } = useTheme();

  return (
    <div className="mx-auto max-w-2xl text-center relative z-10">
      <h1 className="mb-8 text-4xl font-bold" style={{ color: "var(--color-text)" }}>
        Focus Timer
      </h1>

      <div className="cozy-card mb-8 p-8 relative overflow-hidden">
        {/* Background Animation Layer */}
        <div className="absolute inset-0 pointer-events-none flex items-center justify-center opacity-20">
          <AnimatePresence mode="wait">
            {theme === "light" ? (
              <motion.div
                key="sun"
                initial={{ scale: 0, rotate: -90 }}
                animate={{ scale: 1, rotate: 0 }}
                exit={{ scale: 0, rotate: 90 }}
                transition={{ duration: 0.5 }}
                className="relative"
              >
                {/* Sun Rays */}
                <motion.div
                  animate={{ rotate: 360 }}
                  transition={{ duration: 20, repeat: Infinity, ease: "linear" }}
                  className="absolute inset-0 -m-12"
                >
                  {[...Array(12)].map((_, i) => (
                    <div
                      key={i}
                      className="absolute h-4 w-4 bg-[var(--color-accent)] rounded-full"
                      style={{
                        top: "50%",
                        left: "50%",
                        transform: `rotate(${i * 30}deg) translate(120px) translateY(-50%)`,
                      }}
                    />
                  ))}
                </motion.div>
                {/* Sun Core */}
                <motion.div
                  animate={{ scale: isActive ? [1, 1.1, 1] : 1 }}
                  transition={{ duration: 2, repeat: Infinity }}
                  className="h-48 w-48 rounded-full bg-[var(--color-accent)] blur-xl"
                />
                <div className="absolute inset-0 h-48 w-48 rounded-full bg-[var(--color-accent)] opacity-50" />
              </motion.div>
            ) : (
              <motion.div
                key="moon"
                initial={{ scale: 0, rotate: 90 }}
                animate={{ scale: 1, rotate: 0 }}
                exit={{ scale: 0, rotate: -90 }}
                transition={{ duration: 0.5 }}
                className="relative"
              >
                {/* Stars */}
                {[...Array(20)].map((_, i) => (
                  <motion.div
                    key={i}
                    animate={{ opacity: [0.2, 1, 0.2], scale: [0.8, 1.2, 0.8] }}
                    transition={{
                      duration: 2 + Math.random() * 2,
                      repeat: Infinity,
                      delay: Math.random() * 2,
                    }}
                    className="absolute h-1 w-1 bg-white rounded-full"
                    style={{
                      top: `${Math.random() * 300 - 150}px`,
                      left: `${Math.random() * 300 - 150}px`,
                    }}
                  />
                ))}
                {/* Moon Glow */}
                <motion.div
                  animate={{ scale: isActive ? [1, 1.05, 1] : 1 }}
                  transition={{ duration: 3, repeat: Infinity }}
                  className="h-48 w-48 rounded-full bg-[var(--color-primary)] blur-xl opacity-30"
                />
                {/* Moon Shape */}
                <svg viewBox="0 0 100 100" className="absolute inset-0 h-48 w-48 text-[var(--color-primary)] fill-current opacity-80">
                  <path d="M50 10 A40 40 0 1 0 50 90 A30 30 0 1 1 50 10 Z" />
                </svg>
              </motion.div>
            )}
          </AnimatePresence>
        </div>

        {/* Timer Controls */}
        <div className="relative z-10">
          <div className="mb-8 flex justify-center gap-4">
            <button
              onClick={() => setMode("focus")}
              className={clsx(
                "cozy-btn flex items-center px-4 py-2",
                mode === "focus"
                  ? "bg-[var(--color-primary)] text-white"
                  : "bg-[var(--color-card)] text-[var(--color-text)]"
              )}
            >
              <Brain className="mr-2 h-4 w-4" />
              Focus
            </button>
            <button
              onClick={() => setMode("shortBreak")}
              className={clsx(
                "cozy-btn flex items-center px-4 py-2",
                mode === "shortBreak"
                  ? "bg-[var(--color-secondary)] text-white"
                  : "bg-[var(--color-card)] text-[var(--color-text)]"
              )}
            >
              <Coffee className="mr-2 h-4 w-4" />
              Short Break
            </button>
            <button
              onClick={() => setMode("longBreak")}
              className={clsx(
                "cozy-btn flex items-center px-4 py-2",
                mode === "longBreak"
                  ? "bg-[var(--color-accent)] text-white"
                  : "bg-[var(--color-card)] text-[var(--color-text)]"
              )}
            >
              <Zap className="mr-2 h-4 w-4" />
              Long Break
            </button>
          </div>

          <div className="mb-8 text-9xl font-bold tracking-widest drop-shadow-sm" style={{ color: "var(--color-text)" }}>
            {formatTime(timeLeft)}
          </div>

          <div className="flex justify-center gap-6">
            <button
              onClick={toggleTimer}
              className="cozy-btn flex h-16 w-16 items-center justify-center rounded-full bg-[var(--color-primary)] text-white hover:bg-[var(--color-primary)] hover:scale-105 transition-transform"
            >
              {isActive ? (
                <Pause className="h-8 w-8 fill-current" />
              ) : (
                <Play className="ml-1 h-8 w-8 fill-current" />
              )}
            </button>
            <button
              onClick={resetTimer}
              className="cozy-btn flex h-16 w-16 items-center justify-center rounded-full bg-[var(--color-card)] text-[var(--color-text)] hover:bg-[var(--color-danger)] hover:text-white hover:scale-105 transition-transform"
            >
              <RotateCcw className="h-8 w-8" />
            </button>
          </div>
        </div>
      </div>

      <div className="cozy-card p-6">
        <h3 className="mb-2 text-xl font-bold" style={{ color: "var(--color-text)" }}>Today's Focus</h3>
        <p className="text-3xl font-bold" style={{ color: "var(--color-secondary)" }}>0h 0m</p>
      </div>
    </div>
  );
}
